import {
  GET_ALL_LIST_SUCCESS, GET_ALL_LIST_FAILURE, INSERT_LIST_SUCCESS, INSERT_LIST_FAILURE,
  UPDATE_LIST_SUCCESS, UPDATE_LIST_FAILURE,
  DELETE_LIST_SUCCESS, DELETE_LIST_FAILURE, GET_SINGLE_LIST_SUCCESS, GET_SINGLE_LIST_FAILURE,
  POST_LIST_FAILURE, POST_LIST_SUCCESS, GET_ALL_RECORDS_SUCCESS, GET_ALL_RECORDS_FAILURE, GET_ALL_ENTRIES_SUCCESS,
  GET_ALL_ENTRIES_FAILURE, SAVE_IMPORTDATA_SUCCESS, SAVE_IMPORTDATA_FAILURE,
  GET_BANKS_TRANSACTIONS_FAILURE, GET_BANKS_TRANSACTIONS_SUCCESS, SAVE_BANK_RECONCILIATION_FAILURE,
  SAVE_BANK_RECONCILIATION_SUCCESS, INSERT_INSERTRECORD_SUCCESS, INSERT_INSERTRECORD_FAILURE,
  DASH_BOARD_DATA_SUCCESS,
  DASH_BOARD_DATA_FAILURE,
  CREATE_OR_UPDATE_LIST_SUCCESS,
  CREATE_OR_UPDATE_LIST_FAILURE

} from "../types";
import { reject } from "q";
import { put } from 'redux-saga/effects';
import { getAll, insertRec, updateRec, deleteRec, singleGet, postRec, saveImportData, getBanksTransaction, saveBankReconiliation, insertRecord, getDashboard, createOrUpdate } from "../../utils/consts/api";

//calling saga functions 

export function* getAllSaga(action) {
  try {
    const resp = yield getAll(action.payload);
    yield put({ type: GET_ALL_LIST_SUCCESS, payload: resp.data });

  } catch (error) {
    yield put({ type: GET_ALL_LIST_FAILURE, payload: error });
    reject(error);
  }
}

