import axios from 'axios'
import constants from 'utilities/constants';
const constant = constants.getConstant();

const instance = axios.create({
    baseURL: constant.url3,
    headers: {
        "Access-Control-Allow-Origin": "*"
    }
});

instance.interceptors.request.use(config => {
    const token = JSON.parse(sessionStorage.getItem('webtoken'));
    const companyId = JSON.parse(sessionStorage.getItem('CompID'));
    const companyCode = JSON.parse(sessionStorage.getItem('CompCode'));
    const session = JSON.parse(sessionStorage.getItem('Session'));

    config.headers.Authorization = `Bearer ${token}`;
    config.headers['CompID'] = companyId;
    config.headers['Session'] = session;
    config.headers['CompCode'] = companyCode;

    return config;
});

export default instance;