import {
    LOGIN, LOGIN_SUCCESS, LOGIN_FAILURE, GET_USER_RIGHTS_SUCCESS, GET_USER_RIGHTS_FAILURE, GET_USER_ACTIONS_SUCCESS,
    GET_USER_ACTIONS_FAILURE, LOGOUT, LOGIN_NULL, LOGIN_MANUFACTURE, LOGIN_SUCCESS_FOR_MANUFACTURE, LOGIN_FAILURE_FOR_MANUFACTURE,
    LOGIN_NULL_FOR_MANUFACTURE
} from "../types";

const INITIAL_STATE = {
    isLoading: true,
    user: null,
    error: null,
    isAuthenticated: false,
    // LOGIN_MANUFACTURE
    isLoadingManufacture: true,
    userManufacture: null,
    errorManufacture: null,
    isAuthenticatedManufacture: false

};

//functions for reducers 
export const AuthReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {
        case LOGIN:
            return { ...state, isLoading: true, error: null };
        case LOGIN_FAILURE:
            return { ...state, user: null, userRights: null, isAuthenticated: false, isLoading: false, error: action.payload };
        case LOGIN_SUCCESS:
            return { ...state, user: action.payload, isAuthenticated: true, error: null };
        //LOGIN MANUFACTURE
        case LOGIN_MANUFACTURE:
            return { ...state, isLoadingManufacture: true, errorManufacture: null };
        case LOGIN_FAILURE_FOR_MANUFACTURE:
            return { ...state, userManufacture: null, userRights: null, isAuthenticatedManufacture: false, isLoading: false, errorManufacture: action.payload };
        case LOGIN_SUCCESS_FOR_MANUFACTURE:
            return { ...state, userManufacture: action.payload, isAuthenticatedManufacture: true, error: null };

        case GET_USER_RIGHTS_SUCCESS:
            return { ...state, userRights: action.payload, isAuthenticated: true, isLoading: false, error: null };
        case GET_USER_RIGHTS_FAILURE:
            return { ...state, user: null, userRights: null, isAuthenticated: false, isLoading: false, error: action.payload };

        case GET_USER_ACTIONS_SUCCESS:
            return { ...state, userActions: action.payload, isAuthenticated: true, isLoading: false, error: null };
        case GET_USER_ACTIONS_FAILURE:
            return { ...state, user: null, userActions: null, isAuthenticated: false, isLoading: false, error: action.payload };
        case LOGOUT:
            return { ...state, user: null, userRights: null, userActions: null, isAuthenticated: null, isLoading: null, error: null };

        case LOGIN_NULL:
            return { ...state, user: null, userRights: null, userActions: null, isAuthenticated: null, isLoading: null, error: null };
        default:
            return state;
    }
}