import { takeEvery, all } from 'redux-saga/effects';

import {
    GET_SINGLE_LIST, LOGIN, GET_ALL_LIST, GET_ALL_RECORDS, GET_ALL_ENTRIES, GET_LOOKUP_DATA, INSERT_LIST, UPDATE_LIST,
    DELETE_LIST, GET_LOOKUP_2_DATA, GET_LOOKUP_3_DATA, GET_LOOKUP_4_DATA, GET_LOOKUP_5_DATA, GET_LOOKUP_6_DATA, LOGIN_MANUFACTURE,
    GET_LOOKUP_7_DATA, GET_LOOKUP_8_DATA, GET_LOOKUP_9_DATA, GET_LOOKUP_10_DATA, GET_LOOKUP_11_DATA, GET_LOOKUP_12_DATA, CREATE_OR_UPDATE_LIST,
    GET_LOOKUP_13_DATA, GET_LOOKUP_14_DATA, GET_LOOKUP_15_DATA, POST_LIST, SAVE_IMPORTDATA, GET_BANKS_TRANSACTIONS, SAVE_BANK_RECONCILIATION, INSERT_INSERTRECORD, DASH_BOARD_DATA,
    GET_INVENTORY_LOOKUP_DATA,GET_INVENTORY_LOOKUP_DATA1,GET_INVENTORY_LOOKUP_DATA2
} from './types';
import { loginSaga, loginManufactureModuleSaga } from './sagas/Login';
import { getAllSaga, getRecSaga, getEntriesSaga, InsertSaga, UpdateSaga, DeleteSaga, singleGetSaga, postRecSaga, getBanksTransactionSaga, saveBankReconiliationSaga, InsertRecordSaga, getDashBoard, createOrUpdateSaga } from './sagas/AllActions';
import {
    getLookupSaga, getLookup2Saga, getLookup3Saga, getLookup4Saga, getLookup5Saga, getLookup6Saga,
    getLookup7Saga, getLookup8Saga, getLookup9Saga, getLookup10Saga, getLookup11Saga, getLookup12Saga,
    getLookup13Saga, getLookup14Saga, getLookup15Saga ,getInventoryLookupSaga ,getInventoryLookup1Saga,getInventoryLookup2Saga
} from './sagas/Lookup';

//index for watch files
export function* watchSBS() {
    yield all([

        takeEvery(DASH_BOARD_DATA, getDashBoard),
        takeEvery(LOGIN, loginSaga),
        takeEvery(LOGIN_MANUFACTURE, loginManufactureModuleSaga),
        takeEvery(GET_ALL_LIST, getAllSaga),
        takeEvery(GET_ALL_RECORDS, getRecSaga),
        takeEvery(GET_ALL_ENTRIES, getEntriesSaga),
        takeEvery(GET_SINGLE_LIST, singleGetSaga),
        takeEvery(GET_LOOKUP_DATA, getLookupSaga),
        takeEvery(GET_LOOKUP_2_DATA, getLookup2Saga),
        takeEvery(GET_LOOKUP_3_DATA, getLookup3Saga),
        takeEvery(GET_LOOKUP_4_DATA, getLookup4Saga),
        takeEvery(GET_LOOKUP_5_DATA, getLookup5Saga),
        takeEvery(GET_LOOKUP_6_DATA, getLookup6Saga),
        takeEvery(GET_LOOKUP_7_DATA, getLookup7Saga),
        takeEvery(GET_LOOKUP_8_DATA, getLookup8Saga),
        takeEvery(GET_LOOKUP_9_DATA, getLookup9Saga),
        takeEvery(GET_LOOKUP_10_DATA, getLookup10Saga),
        takeEvery(GET_LOOKUP_11_DATA, getLookup11Saga),
        takeEvery(GET_LOOKUP_12_DATA, getLookup12Saga),
        takeEvery(GET_LOOKUP_13_DATA, getLookup13Saga),
        takeEvery(GET_LOOKUP_14_DATA, getLookup14Saga),
        takeEvery(GET_LOOKUP_15_DATA, getLookup15Saga),
        takeEvery(GET_INVENTORY_LOOKUP_DATA, getInventoryLookupSaga),
        takeEvery(GET_INVENTORY_LOOKUP_DATA1, getInventoryLookup1Saga),
        takeEvery(GET_INVENTORY_LOOKUP_DATA2, getInventoryLookup2Saga),
        // 
        takeEvery(INSERT_LIST, InsertSaga),
        takeEvery(CREATE_OR_UPDATE_LIST,createOrUpdateSaga),
        takeEvery(UPDATE_LIST, UpdateSaga),
        takeEvery(DELETE_LIST, DeleteSaga),
        takeEvery(POST_LIST, postRecSaga),
        takeEvery(INSERT_INSERTRECORD, InsertRecordSaga),
        // takeEvery(SAVE_IMPORTDATA,saveImportDataSaga),
        takeEvery(GET_BANKS_TRANSACTIONS, getBanksTransactionSaga),
        takeEvery(SAVE_BANK_RECONCILIATION, saveBankReconiliationSaga)
    ]);
}