import {
  LOGIN_SUCCESS, LOGIN_FAILURE, LOGIN_SUCCESS_FOR_MANUFACTURE, LOGIN_FAILURE_FOR_MANUFACTURE
} from "../types";
import { reject } from "q";
import { put } from 'redux-saga/effects';
import { login, loginManufactureModule } from "../../utils/consts/api";

//calling saga functions 
export function* loginSaga(action) {
  try {
    const resp = yield login(action.payload);
    yield put({ type: LOGIN_SUCCESS, payload: resp.data });
  } catch (error) {
    yield put({ type: LOGIN_FAILURE, payload: error });
    reject(error);
  }
}

export function* loginManufactureModuleSaga(action) {
  try {
    const resp = yield loginManufactureModule(action.payload);
    yield put({ type: LOGIN_SUCCESS_FOR_MANUFACTURE, payload: resp.data });
  } catch (error) {
    yield put({ type: LOGIN_FAILURE_FOR_MANUFACTURE, payload: error });
    reject(error);
  }
}

