import {
  GET_LOOKUP_DATA_SUCCESS, GET_LOOKUP_DATA_FAILURE, GET_LOOKUP_2_DATA_SUCCESS, GET_LOOKUP_3_DATA_SUCCESS,
  GET_LOOKUP_2_DATA_FAILURE, GET_LOOKUP_3_DATA_FAILURE, GET_LOOKUP_4_DATA_SUCCESS, GET_LOOKUP_4_DATA_FAILURE,
  GET_LOOKUP_5_DATA_SUCCESS, GET_LOOKUP_5_DATA_FAILURE, GET_LOOKUP_6_DATA_SUCCESS, GET_LOOKUP_6_DATA_FAILURE,
  GET_LOOKUP_7_DATA_SUCCESS, GET_LOOKUP_7_DATA_FAILURE, GET_LOOKUP_8_DATA_SUCCESS, GET_LOOKUP_8_DATA_FAILURE,
  GET_LOOKUP_9_DATA_SUCCESS, GET_LOOKUP_9_DATA_FAILURE, GET_LOOKUP_10_DATA_SUCCESS, GET_LOOKUP_10_DATA_FAILURE,
  GET_LOOKUP_11_DATA_SUCCESS, GET_LOOKUP_11_DATA_FAILURE, GET_LOOKUP_12_DATA_SUCCESS, GET_LOOKUP_12_DATA_FAILURE,
  GET_LOOKUP_13_DATA_SUCCESS, GET_LOOKUP_13_DATA_FAILURE, GET_LOOKUP_14_DATA_SUCCESS, GET_LOOKUP_14_DATA_FAILURE,
  GET_LOOKUP_15_DATA_SUCCESS, GET_LOOKUP_15_DATA_FAILURE, GET_INVENTORY_LOOKUP_DATA_SUCCESS, GET_INVENTORY_LOOKUP_DATA_FAILURE,
  GET_INVENTORY_LOOKUP_DATA1_SUCCESS, GET_INVENTORY_LOOKUP_DATA1_FAILURE,GET_INVENTORY_LOOKUP_DATA2_SUCCESS,
  GET_INVENTORY_LOOKUP_DATA2_FAILURE

} from "../types";
import { reject } from "q";
import { put } from 'redux-saga/effects';
import { getLookup, getInventoryLookup } from "../../utils/consts/api";

//calling saga functions 
export function* getLookupSaga(action) {
  try {
    const resp = yield getLookup(action.payload);
    yield put({ type: GET_LOOKUP_DATA_SUCCESS, payload: resp.data });

  } catch (error) {
    yield put({ type: GET_LOOKUP_DATA_FAILURE, payload: error });
    reject(error);
  }
}

