import axios from './axios'
import constants from '../../utilities/constants';
const constant = constants.getConstant();

//Login API
export const login = (payload) => axios.post(`${constant.url}/api/v1/Get`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer' } });
export const loginManufactureModule = (payload) => axios.post(`${constant.url2}/api/v1/Auth/LogIn`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer' } });
export const createOrUpdateRec = (payload) => axios.post(`${constant.url}/api/v1/Manufacturing/Create`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': `Bearer${JSON.parse(sessionStorage.getItem('jsonWebToken'))}` } });
export const getUserRights = (payload) => axios.post(`${constant.url}/api/v1/user/1`, { ...payload[0] }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const getUserActions = (payload) => axios.get(`${constant.url}/api/v1/user/11`, { ...payload[0] }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });

// FOR MANUFACTURING MODULE
export const getAll = (FormName) => { return axios.get(`/api/v1/Manufacturing/GetAll?FormName=${FormName}`) }
export const createOrUpdate = (payload) => { return axios.post(`/api/v1/Manufacturing/Create`, { ...payload }) }
export const deleteRec = (payload) => { return axios.delete(`/api/v1/Manufacturing/Delete?FormName=${payload['FormName']}&${payload['FormCode']}=${payload['FormNameIDValue']}`) }
// export const singleGet = (payload) => { return axios.get(`/api/v1/Manufacturing/Get?FormName=${payload['FormNameID']}&${payload['FormCode']}=${payload['FormNameIDValue']}`) }
                                                                                // Get?FormName=Machine&FormCode=MachineCode&FormNameIDValue=001
export const singleGet = (payload) => { return axios.get(`/api/v1/Manufacturing/Get?${payload}`) }
export const getLookup = (payload) => { return axios.get(`/api/v1/Manufacturing/LookUp?${payload}`) }
export const getInventoryLookup = (payload) => { return axios.get(`/api/v1/Inventory/LookUp?${payload}`) }


export const insertRecord = (payload) => axios.post(`${constant.url}/api/v1/PFPost`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const insertRec = (payload) => axios.post(`${constant.url}/api/v1/Create`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const postRec = (payload) => axios.post(`${constant.url}/api/v1/Post`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const currencyReval = (payload) => axios.post(`${constant.url}/api/v1/CurrencyRevaluation`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const updateRec = (payload) => axios.put(`${constant.url}/api/v1/Update/${payload.ID}`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const getBanksTransaction = (payload) => axios.put(`${constant.url}/api/v1/BankReconciliation/${payload.BAcctCode}`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const saveBankReconiliation = (payload) => axios.post(`${constant.url}/api/v1/BankReconciliation`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
export const getDashboard = (payload) => axios.post(`${constant.url}/api/v1/Dashboard`, { ...payload }, { headers: { 'Content-Type': 'application/json; charset=utf-8', 'Authorization': 'Bearer', 'BranchCode': JSON.parse(sessionStorage.getItem('Branch')).BranchCode, "UserID": JSON.parse(sessionStorage.getItem('UserID')) } });
