import {
    GET_ALL_LIST_SUCCESS, GET_ALL_LIST_FAILURE, GET_ALL_RECORDS_SUCCESS, GET_ALL_RECORDS_FAILURE, GET_ALL_ENTRIES_SUCCESS,
    GET_ALL_ENTRIES_FAILURE, INSERT_LIST_SUCCESS, INSERT_LIST_FAILURE, UPDATE_LIST_SUCCESS, UPDATE_LIST_FAILURE,
    DELETE_LIST_SUCCESS, DELETE_LIST_FAILURE, NULL_LIST, GET_SINGLE_LIST_SUCCESS, GET_SINGLE_LIST_FAILURE,
    POST_LIST_FAILURE, POST_LIST_SUCCESS, SAVE_IMPORTDATA_SUCCESS, SAVE_IMPORTDATA_FAILURE, GET_BANKS_TRANSACTIONS_FAILURE, GET_BANKS_TRANSACTIONS_SUCCESS,
    SAVE_BANK_RECONCILIATION_FAILURE, SAVE_BANK_RECONCILIATION_SUCCESS, POST_CURRENCY_REVALUATION_SUCCESS, POST_CURRENCY_REVALUATION_FAILURE, INSERT_INSERTRECORD_FAILURE, INSERT_INSERTRECORD_SUCCESS
    // POST_CURRENCY_REVALUATION_SUCCESS,
    // POST_CURRENCY_REVALUATION_FAILURE,
    , DASH_BOARD_DATA_SUCCESS,
    DASH_BOARD_DATA_FAILURE,
    CREATE_OR_UPDATE_LIST_SUCCESS,
    CREATE_OR_UPDATE_LIST_FAILURE

} from "../types";

const INITIAL_STATE = {
    records: null,
    entries: null,
    list: null,
    error: null,
    insertData: null,
    updateData: null,
    deleteData: null, saveProps: null,
    postList: null,
    saveImportData: null,
    bankTransactions: null,
    saveBankReconciliation: null,
    pfRecords: null,
    dashboard: null,
    createOrUpdateData: null
};

//functions for reducers 
export const AllActionsReducer = (state = INITIAL_STATE, action) => {
    switch (action.type) {

        case POST_CURRENCY_REVALUATION_SUCCESS:
            return { ...state, ll: action.payload };
        case POST_CURRENCY_REVALUATION_FAILURE:
            return { ...state, ll: null, error: action.payload };

        case GET_ALL_LIST_SUCCESS:
            return { ...state, list: action.payload };
        case GET_ALL_LIST_FAILURE:
            return { ...state, list: null, error: action.payload };

        case GET_ALL_RECORDS_SUCCESS:
            return { ...state, records: action.payload };
        case GET_ALL_RECORDS_FAILURE:
            return { ...state, records: null, error: action.payload };

        case GET_BANKS_TRANSACTIONS_SUCCESS:
            return { ...state, bankTransactions: action.payload };
        case GET_BANKS_TRANSACTIONS_FAILURE:
            return { ...state, bankTransactions: null, error: action.payload };

        case GET_ALL_ENTRIES_SUCCESS:
            return { ...state, entries: action.payload };
        case GET_ALL_ENTRIES_FAILURE:
            return { ...state, entries: null, error: action.payload };

        case SAVE_BANK_RECONCILIATION_SUCCESS:
            return { ...state, saveBankReconciliation: action.payload };

        case SAVE_BANK_RECONCILIATION_FAILURE:

            return { ...state, saveBankReconciliation: null, error: action.payload };

        case INSERT_LIST_SUCCESS:
            return { ...state, insertData: action.payload };

        case INSERT_LIST_FAILURE:
            return { ...state, insertData: null, error: action.payload };

        case CREATE_OR_UPDATE_LIST_SUCCESS:
            return { ...state, createOrUpdateData: action.payload };

        case CREATE_OR_UPDATE_LIST_FAILURE:
            return { ...state, createOrUpdateData: null, error: action.payload };

        case INSERT_INSERTRECORD_SUCCESS:
            return { ...state, pfRecords: action.payload };

        case INSERT_INSERTRECORD_FAILURE:
            return { ...state, pfRecords: null, error: action.payload };

        case SAVE_IMPORTDATA_SUCCESS:
            return { ...state, saveImportData: action.payload };

        case SAVE_IMPORTDATA_FAILURE:

            return { ...state, saveImportData: null, error: action.payload };


        case UPDATE_LIST_SUCCESS:
            return { ...state, updateData: action.payload };

        case UPDATE_LIST_FAILURE:

            return { ...state, updateData: null, error: action.payload };

        case DELETE_LIST_SUCCESS:

            return { ...state, deleteData: action.payload };

        case DELETE_LIST_FAILURE:

            return { ...state, deleteData: null, error: action.payload };


        case GET_SINGLE_LIST_SUCCESS:
            return { ...state, singleList: action.payload };
        case GET_SINGLE_LIST_FAILURE:
            return { ...state, singleList: null, error: action.payload };


        case POST_LIST_SUCCESS:
            return { ...state, postList: action.payload };
        case POST_LIST_FAILURE:
            return { ...state, postList: null, error: action.payload };

        case DASH_BOARD_DATA_SUCCESS:
            return { ...state, dashboard: action.payload };
        case DASH_BOARD_DATA_FAILURE:
            return { ...state, dashboard: null, error: action.payload };








        case NULL_LIST:

            return {
                ...state,
                list: null,
                error: null,
                insertData: null,
                updateData: null,
                deleteData: null,
                saveProps: null,
                singleList: null,
                postList: null,
                records: null,
                entries: null,
                dashboard: null,
                saveImportData: null,
                saveBankReconciliation: null,
                pfRecords: null,
                bankTransactions: null,
                createOrUpdateData: null
            };
        default:
            return state;

    }
}