import { lazy } from 'react';
import MainLayout from 'layout/MainLayout';
import Loadable from 'ui-component/Loadable';
import AuthGuard from 'utils/route-guard/AuthGuard';

// --------------------------Setup---------------------------//
const DashboardDefault = Loadable(lazy(() => import('views/dashboard/Default')));
//-----------------------Setup--------------------------//
const Shift = Loadable(lazy(() => import('views/Setup/ShiftMaintenance')));
const Machine = Loadable(lazy(() => import('views/Setup/MachineMaintenance')));
const Routing = Loadable(lazy(() => import('views/Setup/RoutingMaintenance')));
const Stages = Loadable(lazy(() => import('views/Setup/ProductionStages')));
const BOM = Loadable(lazy(() => import('views/Setup/BillOfMaterial')));
//-----------------------Transactions--------------------------//
const MOEntry = Loadable(lazy(() => import('views/Transactions/MoEntry')));
const MOIssuance = Loadable(lazy(() => import('views/Transactions/MoIssuance')));
const MOReceipt = Loadable(lazy(() => import('views/Transactions/MoRecepit')));



const MainRoutes = {
    path: '/',
    element: (
        <AuthGuard>
            <MainLayout />
        </AuthGuard>
    ),

    children: [
        {
            path: '/Dashboard',
            element: <DashboardDefault />
        },
        {
            path: '/Shift',
            element: <Shift />
        },
        {
            path: '/Machine',
            element: <Machine />
        },
        {
            path: '/Routing',
            element: <Routing />
        },
        {
            path: '/RoutingStages',
            element: <Stages />
        },
        {
            path: '/BOM',
            element: <BOM />
        },
        {
            path: '/MOEntry',
            element: <MOEntry />
        },
        {
            path: '/MOIssuance',
            element: <MOIssuance />
        },
        {
            path: '/MOReceipt',
            element: <MOReceipt />
        },
    ]
};

export default MainRoutes;
